import React from "react";
import "./CreditCardItem.css";


import {useSetUserItemDefaultMutation, useRemoveUserItemMutation} from "../../Redux/Services/userApi";

import {ReactComponent as DeleteIcon} from "../../Assets/icons/delete_icon.svg";
import visa from "../../Assets/images/visa.png";
import musterCard from "../../Assets/images/muster_card.png";

export default function CreditCardItem({item}) {
	const [setDefault] = useSetUserItemDefaultMutation();
	const [removeCard] = useRemoveUserItemMutation();

	return (
		<div className={item.default ? "credit-card-item credit-card-item__selected" : "credit-card-item"}>
			<div className="credit-card-item__line">
				<img src={item.company === "Visa" ? visa : musterCard} />
				{!item.default && (
					<button onClick={() => removeCard({data: {cards: {_id: item._id}}})}>
						<DeleteIcon />
					</button>
				)}
			</div>
			<div className="credit-card-item__line">
				<span className="credit-card-item__number">{item?.number}</span>
				<input type="radio" checked={item.default} onChange={() => setDefault({data: {cards: {_id: item._id}}})} />
			</div>
			<div className="credit-card-item__line">
				<span>{item.company}</span>
				<span>{item.date}</span>
			</div>
		</div>
	);
}
