import React from "react";
import "./AddressItem.css";

import {useSetUserItemDefaultMutation, useRemoveUserItemMutation} from "../../Redux/Services/userApi";

import {ReactComponent as EditIcon} from "../../Assets/icons/edit_icon.svg";
import {ReactComponent as DeleteIcon} from "../../Assets/icons/delete_icon.svg";

export default function AddressItem({item, handleEditAddress}) {
	const [setDefault] = useSetUserItemDefaultMutation();
	const [removeAddress] = useRemoveUserItemMutation();

	return (
		<div className={item.default ? "address-item address-item__selected" : "address-item"}>
			<div className="address-item__left-side">
				<h4>{item.addressName}</h4>
				<p>
					{item.street}, {item.city}.<br /> Zip Code: {item.zipCode}
				</p>
			</div>
			<div className="address-item__right-side">
				<div>
					<button onClick={() => handleEditAddress(item)}>
						<EditIcon />
					</button>
					{!item.default && (
						<button onClick={() => removeAddress({data: {address: {_id: item._id}}})}>
							<DeleteIcon />
						</button>
					)}
				</div>
				<input type="radio" checked={item.default} onChange={() => setDefault({data: {address: {_id: item._id}}})} />
			</div>
		</div>
	);
}
