import {useRef, useState, useReducer} from "react";
import "./AddCreditCard.css";

import {useUpdateUserItemMutation} from "../../Redux/Services/userApi";

import {ReactComponent as BackIcon} from "../../Assets/icons/back_icon.svg";
import visa from "../../Assets/images/visa.png";
import musterCard from "../../Assets/images/muster_card.png";

import ErrAlert from "../ErrAlert";

export default function AddCreditCard() {
	const cardRef = useRef(null);
	const idRef = useRef(null);
	const dateRef = useRef(null);
	const cvvRef = useRef(null);
	const [img, setImg] = useState(null);
	const [err, setErr] = useState(null);
	const [showInputCard, setShowInputCard] = useState(false);
	const [addAddress] = useUpdateUserItemMutation();

	const handleCardIDChange = (e) => {
		var v = e.target.value.replace(/\s+/g, "").replace(/[^0-9]/gi, "");
		var matches = v.match(/\d{4,16}/g);
		var match = (matches && matches[0]) || "";
		var parts = [];
		for (let i = 0, len = match.length; i < len; i += 4) {
			parts.push(match.substring(i, i + 4));
		}

		if (parts[0] && parts[0] === "4580") setImg(visa);
		else if (parts[0]?.startsWith("541")) setImg(musterCard);
		else setImg(null);

		idRef.current.value = parts.length > 0 ? parts.join(" ") : v;

		if (parts[3]?.length === 4) dateRef.current.focus();
	};

	const handleDateChange = (e) => {
		var v = e.target.value.replace(/\s+/g, "").replace(/[^0-9]/gi, "");
		var matches = v.match(/\d{2,4}/g);
		var match = (matches && matches[0]) || "";
		var parts = [];
		for (let i = 0, len = match.length; i < len; i += 2) {
			parts.push(match.substring(i, i + 2));
		}
		dateRef.current.value = parts.length > 0 ? parts.join("/") : v;

		if (parts.length === 0 && v === "") idRef.current.focus();

		if (parts[1]?.length === 2 && idRef.current.value.length === 19)
			setTimeout(() => {
				cardRef.current.classList.add("add-credit-card__show-back");
				cvvRef.current.focus();
			}, 300);
	};

	const handleCVVChange = (e) => {
		var v = e.target.value.replace(/\s+/g, "").replace(/[^0-9]/gi, "");
		var matches = v.match(/\d{3}/g);
		cvvRef.current.value = matches?.length > 0 ? matches[0] : v;
	};

	const handleAdd = () => {
		if (!showInputCard) setShowInputCard(true);
		else if (idRef.current?.value.length !== 19) showErr("Invalid Card Number");
		else if (!img) showErr("We support only Visa and Muster Card");
		else if (dateRef.current?.value.length !== 5) showErr("Invalid Date");
		else if (cvvRef.current?.value.length !== 3) showErr("Invalid CVV");
		else {
			var data = {
				cards: {
					number: idRef.current.value,
					date: dateRef.current.value,
					cvv: cvvRef.current.value,
					company: img === visa ? "Visa" : "Muster Card",
				},
			};
			addAddress({type: "add", data})
				.unwrap()
				.then((res) => {
					setTimeout(() => {
						if (!res.success) {
							if (typeof res.data === "string") showErr(res.data);
						} else setShowInputCard(false);
					});
				});
		}
	};

	const showErr = (t) => {
		setErr(t);
		setTimeout(() => setErr(null), 2000);
	};

	return (
		<div className="add-credit-card">
			{showInputCard && (
				<div className="add-credit-card__container">
					<div className="add-credit-card__inner" ref={cardRef}>
						<div className="add-credit-card__front">
							<div className="add-credit-card__front-inner">
								<input
									className="add-credit-card__id"
									type="text"
									placeholder="1234 4567 7891 123"
									ref={idRef}
									onChange={(e) => handleCardIDChange(e)}
								/>
								<div className="add-credit-card__line">
									<input className="add-credit-card__date" type="text" placeholder="MM/YY" ref={dateRef} onChange={(e) => handleDateChange(e)} />
								</div>
							</div>
							<div className="add-credit-card__img-container">{img && <img src={img} />}</div>
						</div>

						<div className="add-credit-card__back">
							<button onClick={() => cardRef.current.classList.remove("add-credit-card__show-back")}>
								<BackIcon />
							</button>
							<div className="add-credit-card__black-line"></div>
							<input className="add-credit-card__cvv" type="text" ref={cvvRef} placeholder="CVV" onChange={(e) => handleCVVChange(e)} />
						</div>
					</div>
				</div>
			)}
			{err && <ErrAlert text={err} />}
			<button className="primary-button" onClick={handleAdd}>
				Add
			</button>
		</div>
	);
}
