import {useEffect, useRef} from "react";
import "./Header.css";

import {Link} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {changeCartDisplay, changeWishlistDisplay} from "../Redux/Reducers/styleSlice";

import {ReactComponent as OrdersIcon} from "../Assets/icons/orders_icon.svg";
import {ReactComponent as CartIconm} from "../Assets/icons/cart_icon.svg";
import {ReactComponent as WishListIcon} from "../Assets/icons/wishlist_icon.svg";
import {ReactComponent as AvatarIcon} from "../Assets/icons/avatar_icon.svg";

import useIsMobile from "../Hooks/useIsMobile";
import useGetUser from "../Hooks/useGetUser";

import Cart from "../Components/Cart/Cart";
import Wishlist from "../Components/Wishlist/Wishlist";

export default function Header() {
	const isMobile = useIsMobile();
	const styleState = useSelector((state) => state.style);
	const dispatch = useDispatch();
	const cartRef = useRef(null);
	const wishlistRef = useRef(null);
	const user = useGetUser();

	useEffect(() => {
		document.addEventListener(
			"click",
			(e) => {
				cartRef.current?.handleClickOutSide(e) && dispatch(changeCartDisplay(false));
				wishlistRef.current?.handleClickOutSide(e) && dispatch(changeWishlistDisplay(false));
			},
			true,
		);
	}, []);

	return (
		<header className="Header">
			<div className="Header__left-side Header__side-container">
				<Link to="/">
					<h1 className="Header__logo">NOTO</h1>
				</Link>
			</div>
			<div className="Header__right-side  Header__side-container">
				{!isMobile && (
					<>
						<Link to={user.fullName ? "/orders" : "/login"} className="Header__button-container">
							<OrdersIcon />
						</Link>
						<div className="Header__button-container">
							{user.cartItems.length ? (
								<div className="Header__counter Header__cart-counter">
									<span>{user.cartItems.length}</span>
								</div>
							) : null}
							<button
								onClick={() => {
									dispatch(changeCartDisplay(!styleState.cart.isOpen));
								}}
							>
								<CartIconm />
							</button>
							{styleState.cart.isOpen && <Cart ref={cartRef} />}
						</div>
						<div className="Header__button-container">
							{user.wishlistItems.length ? (
								<div className="Header__counter Header__wishlist-counter">
									<span>{user.wishlistItems.length}</span>
								</div>
							) : null}
							<button
								onClick={() => {
									dispatch(changeWishlistDisplay(!styleState.wishlist.isOpen));
								}}
							>
								<WishListIcon />
							</button>
							{styleState.wishlist.isOpen && <Wishlist ref={wishlistRef} />}
						</div>
					</>
				)}

				<Link to={user.fullName ? "/setting" : "/login"}>
					<div className="Header__setting-container">
						<div className="Header__setting">
							<AvatarIcon />
						</div>
					</div>
				</Link>
			</div>
		</header>
	);
}
