import {useState, forwardRef, useImperativeHandle} from "react";
import "./Form.css";

import {useForm} from "react-hook-form";
import Input from "./Input";
import ErrAlert from "../ErrAlert";
import SuccessAlert from "../SuccessAlert";

const calcOptions = (input) => {
	var options = {};
	if (input.required) options = {required: `${input.placeholder} is required`};
	if (input.type === "email")
		options = {
			...options,
			pattern: {
				value: /^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i,
				message: "invalid email address",
			},
		};
	if (input.options)
		options = {
			...options,
			...input.options,
		};
	return options;
};

export default forwardRef(function Form({inputs, submit: {onSubmit, submitTxt}, values}, ref) {
	const {
		register,
		handleSubmit,
		setError,
		setValue,
		formState: {errors},
	} = useForm({mode: "onSubmit", reValidateMode: "onSubmit", values});
	const [err, setErr] = useState(null);
	const [success, setSuccess] = useState(null);

	useImperativeHandle(ref, () => ({
		setErr: (t) => {
			setErr(t);
			setTimeout(() => setErr(null), 2000);
		},
		setInputErr: (fields) => {
			for (var field in fields) {
				setError(field, {type: "manual", message: fields[field]});
			}
		},
		setSuccess: (t) => {
			setSuccess(t);
			setTimeout(() => setSuccess(null), 2000);
		},
		setInputValue: (fields) => {
			for (var field in fields) {
				setValue(field, fields[field]);
			}
		},
	}));

	return (
		<form className="form" onSubmit={handleSubmit(onSubmit)}>
			{inputs.map(
				(input) =>
					input.visible !== false && (
						<Input
							type={input.type || "text"}
							placeholder={input.placeholder}
							name={input.name}
							register={register}
							options={calcOptions(input)}
							err={errors[input.name]?.message}
						/>
					),
			)}
			{err && <ErrAlert text={err} />}
			{success && <SuccessAlert text={success} />}
			<button className="primary-button form__button" type="submit">
				{submitTxt}
			</button>
		</form>
	);
});
