import {useState} from "react";
import "./WishlistItem.css";

import {useDispatch} from "react-redux";
import {useRemoveUserItemMutation, useUpdateUserItemMutation} from "../../Redux/Services/userApi";
import {changeCartDisplay} from "../../Redux/Reducers/styleSlice";

import useGetUser from "../../Hooks/useGetUser";

import {ReactComponent as DeleteIcon} from "../../Assets/icons/delete_icon.svg";

export default function WishlistItem({item}) {
	const dispatch = useDispatch();
	const user = useGetUser();
	const [removeFromWishlist] = useRemoveUserItemMutation();
	const [addToCart] = useUpdateUserItemMutation();

	const handleAddToCart = () => {
		const data = {cartItems: {...item}};
		delete data["cartItems"]["productData"];
		delete data["cartItems"]._id;
		const itemExist = user["cartItems"].filter((cartItem) => cartItem.productID === item.productID && cartItem.color === item.color)[0];
		if (itemExist) data["cartItems"]._id = itemExist._id;
		addToCart({type: itemExist ? "edit" : "add", data})
			.unwrap()
			.then((res) => {
				if (res.success) {
					dispatch(changeCartDisplay(true));
					removeFromWishlist({data: {wishlistItems: {_id: item._id}}});
				}
			});
	};

	return (
		<div className="wishlist-item">
			<div className="wishlist-item__line">
				<img src={item.productData.img} />
				<h4>{item.productData.title}</h4>
				<div>
					<button onClick={() => removeFromWishlist({data: {wishlistItems: {_id: item._id}}})}>
						<DeleteIcon />
					</button>
				</div>
			</div>
			<div className="wishlist-item__line">
				<div>
					<span>{item.size}</span>|<span>{item.color}</span>|<span>X{item.qty}</span>
				</div>
				<span>{item.productData.price}</span>
			</div>
			<div className="wishlist-item__line">
				<button className="primary-button" onClick={handleAddToCart}>
					Add to cart
				</button>
			</div>
		</div>
	);
}
