import {isRejectedWithValue} from "@reduxjs/toolkit";
import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/query/react";
import {getCookie, setCookie} from "../../Util/cookies";
import {history} from "../../Util/Navigate";
import {setUserDataIsFetching} from "../Reducers/flagsSlice";

export const userApi = createApi({
	reducerPath: "userApi",
	baseQuery: fetchBaseQuery({baseUrl: "/api/user/"}),
	tagTypes: ["User"],
	endpoints: (builder) => ({
		getUser: builder.query({
			query: () => ({
				headers: {authorization: `Barrer ${getCookie("accessToken")}`},
			}),
			transformResponse: (res) => res.data,

			providesTags: ["User"],
		}),
		UpdateUserField: builder.mutation({
			query: (params) => ({
				url: "update-field",
				method: "POST",
				headers: {...params.headers, authorization: `Barrer ${getCookie("accessToken")}`},
				body: params.data,
			}),
			providesTags: ["User"],
			async onQueryStarted({}, {dispatch, queryFulfilled}) {
				const {
					data: {success, data},
				} = await queryFulfilled;
				if (success) {
					dispatch(
						userApi.util.updateQueryData("getUser", undefined, (draft) => {
							Object.assign(draft, data);
						}),
					);
				}
			},
		}),
		UpdateUserItem: builder.mutation({
			query: (params) => ({
				url: params.type === "edit" ? "update-field" : "add-item",
				method: "POST",
				headers: {...params.headers, authorization: `Barrer ${getCookie("accessToken")}`},
				body: params.data,
			}),
			providesTags: ["User"],
			async onQueryStarted({}, {dispatch, queryFulfilled}) {
				const {
					data: {success, data},
				} = await queryFulfilled;
				if (success)
					dispatch(
						userApi.util.updateQueryData("getUser", undefined, (draft) => {
							for (var key in data) {
								if (Array.isArray(data[key])) draft[key] = data[key];
								else if (data[key]._id) draft[key] = draft[key].map((item) => (item._id === data[key]._id ? data[key] : item));
								else draft[key] = [...draft[key], data[key]];
							}
							return draft;
						}),
					);
			},
		}),
		SetUserItemDefault: builder.mutation({
			query: (params) => ({
				url: "set-default",
				method: "POST",
				headers: {...params.headers, authorization: `Barrer ${getCookie("accessToken")}`},
				body: params.data,
			}),
			providesTags: ["User"],
			async onQueryStarted(params, {dispatch, queryFulfilled}) {
				const {data} = await queryFulfilled;
				if (data.success)
					dispatch(
						userApi.util.updateQueryData("getUser", undefined, (draft) => {
							for (var key in params.data) {
								draft[key] = draft[key].map((item) => {
									if (item._id === params.data[key]._id) item.default = true;
									else item.default = false;
									return item;
								});
							}
						}),
					);
			},
		}),
		RemoveUserItem: builder.mutation({
			query: (params) => ({
				url: "delete-item",
				method: "POST",
				headers: {...params.headers, authorization: `Barrer ${getCookie("accessToken")}`},
				body: params.data,
			}),
			providesTags: ["User"],
			async onQueryStarted(params, {dispatch, queryFulfilled}) {
				const {data} = await queryFulfilled;
				if (data.success)
					dispatch(
						userApi.util.updateQueryData("getUser", undefined, (draft) => {
							for (var key in params.data) {
								draft[key] = draft[key].filter((item) => item._id !== params.data[key]._id);
							}
						}),
					);
			},
		}),

		pay: builder.mutation({
			query: (params) => ({
				url: "pay",
				method: "POST",
				headers: {...params.headers, authorization: `Barrer ${getCookie("accessToken")}`},
				body: params.data,
			}),
			providesTags: ["User"],
			async onQueryStarted({}, {dispatch, queryFulfilled}) {
				const {
					data: {success, data},
				} = await queryFulfilled;
				if (success)
					dispatch(
						userApi.util.updateQueryData("getUser", undefined, (draft) => {
							for (var key in data) {
								draft[key] = data[key];
							}
							return draft;
						}),
					);
			},
		}),
	}),
});

export const handleUserRegect = (api) => (next) => (action) => {
	if (isRejectedWithValue(action)) {
		try {
			// token forbidden
			if (action.payload.status === 403) {
				api.dispatch(userApi.util.resetApiState());
				setCookie("accessToken", null);
				history.navigate("/");
			}
		} catch {}
	}
	if (action.type.indexOf("userApi/executeMutation") > -1) {
		if (action.type.indexOf("userApi/executeMutation/fulfilled") > -1) {
			api.dispatch(setUserDataIsFetching(false));
		} else if (!api.getState().flags.userDataIsFetching) {
			api.dispatch(setUserDataIsFetching(true));
		} else return next(); //in other call, do nothing
	}
	return next(action);
};

export const {
	useGetUserQuery,
	useUpdateUserFieldMutation,
	useUpdateUserItemMutation,
	useSetUserItemDefaultMutation,
	useRemoveUserItemMutation,
	usePayMutation,
} = userApi;
