import {useRef, useState} from "react";
import "./Setting.css";

import {useNavigate} from "react-router-dom";

import {useUpdateUserFieldMutation} from "../../Redux/Services/userApi";
import {useLogoutMutation} from "../../Redux/Services/authApi";
import useGetUser from "../../Hooks/useGetUser";

import {ReactComponent as LogoutIcon} from "../../Assets/icons/log_out_icon.svg";

import CreditCard from "../../Components/CreditCard/CreditCard";
import Address from "../../Components/Address/Address";
import EditAvatar from "../../Components/EditAvatar";
import Form from "../../Components/Form/Form";

export default function Setting() {
	const user = useGetUser();
	const navigate = useNavigate();
	const [logOut] = useLogoutMutation();
	const [updateUserFields] = useUpdateUserFieldMutation();
	const formRef = useRef(null);

	const handleLogOut = () => {
		logOut()
			.unwrap()
			.then((res) => {
				if (res.success) navigate("/");
			});
	};

	const handleSubmit = (data) => {
		updateUserFields({data})
			.unwrap()
			.then((res) => {
				if (res.success) formRef.current.setSuccess("Fields updated successfully");
				else if (typeof res.data === "string") formRef.current.setErr(res.data);
				else formRef.current.setInputErr(res.data);
			});
	};

	return (
		<div className="page">
			<h1 className="setting__title">Setting</h1>
			<section className="setting__section">
				<h4 className="setting__section-title">Personal</h4>
				<EditAvatar />
				<div className="setting__setting-inputs">
					<Form
						ref={formRef}
						inputs={[
							{
								placeholder: "Full Name",
								name: "fullName",
								required: true,
							},
							{
								placeholder: "Email",
								name: "email",
								type: "email",
								required: true,
							},
							{
								placeholder: "New Password",
								type: "password",
								name: "password",
							},
						]}
						values={{fullName: user.fullName, email: user.email}}
						submit={{onSubmit: handleSubmit, submitTxt: "save changes"}}
					/>
				</div>
			</section>
			<section className="setting__section">
				<h4 className="setting__section-title">Address</h4>
				<Address />
			</section>
			<section className="setting__section">
				<h4 className="setting__section-title">Payment</h4>
				<CreditCard />
			</section>
			<section className="setting__section">
				<button className="setting__logout-button" onClick={handleLogOut}>
					<LogoutIcon />
					<strong>Log out</strong>
				</button>
			</section>
		</div>
	);
}
