import {useState, useRef} from "react";
import "./Item.css";

import {useParams, useNavigate} from "react-router-dom";

import {ReactComponent as Whishlist_icon} from "../../Assets/icons/wishlist_icon.svg";

import {useDispatch, useSelector} from "react-redux";

import {changeCartDisplay, changeWishlistDisplay} from "../../Redux/Reducers/styleSlice";

import {useGetProductQuery} from "../../Redux/Services/productApi";
import {useUpdateUserItemMutation} from "../../Redux/Services/userApi";
import useGetUser from "../../Hooks/useGetUser";

import useGetParams from "./useGetParams";

import Picker from "./Picker";
import ErrAlert from "../../Components/ErrAlert";

export default function Item() {
	const {itemId} = useParams();
	const navigate = useNavigate();
	const dispatch = useDispatch();
	const styeleState = useSelector((state) => state.style);
	const pickerRef = useRef(null);
	const [showAlert, setShowAlert] = useState({show: false, text: ""});
	const user = useGetUser();
	const {data: product, isSuccess} = useGetProductQuery(`product/${itemId}`);
	const params = useGetParams({product, isSuccess});
	const [addItem] = useUpdateUserItemMutation();

	const onAddItem = (path) => {
		if (!user.fullName) navigate("/login");
		if (pickerRef.current.checkInputs() !== "") {
			setShowAlert({show: true, text: pickerRef.current.checkInputs()});
			return;
		}

		setShowAlert({show: false, text: ""});
		const options = pickerRef.current.getItemData();
		var cartID = user[path].filter((item) => item.productID === product._id && item.color === options.color)[0]?._id;
		cartID = cartID ? {_id: cartID} : {};

		const data = {
			[path]: {
				...cartID,
				productID: product._id,
				color: options.color,
				size: options.size,
				qty: options.qty,
			},
		};

		addItem({type: cartID._id ? "edit" : "add", data})
			.unwrap()
			.then((res) => {
				if (res.success) {
					if (path === "cartItems") {
						dispatch(changeCartDisplay(true));
					} else {
						dispatch(changeWishlistDisplay(true));
					}
				}
			});
	};

	return (
		<div className="item page">
			{product && (
				<>
					<div className="item__content-container">
						<h2>{product.title}</h2>
						<strong>${product.price}</strong>
						<Picker colors={product.color[0]} params={params} ref={pickerRef} />
						<small>ID:{product._id?.slice(0, 5)}</small>
						<h4>Description</h4>
						<p>{product.description}</p>
						<button className="primary-button item__button" onClick={() => onAddItem("cartItems")}>
							add to cart
						</button>
						{!styeleState.isMobile && (
							<button className="primary-button item__button" onClick={() => onAddItem("wishlistItems")}>
								add to Wishlist
							</button>
						)}
						{showAlert.show && (
							<div className="item__alert">
								<ErrAlert text={showAlert.text} />
							</div>
						)}
					</div>

					<div className="item__img-container">
						{styeleState.isMobile && (
							<button onClick={() => onAddItem("wishlistItems")}>
								<Whishlist_icon width={20} height={20} />
							</button>
						)}
						<img src={product.img} />
					</div>
				</>
			)}
		</div>
	);
}
