import { useState, useEffect } from "react";
import "./Input.css";

import { ReactComponent as ErrIcon } from "../../Assets/icons/err_icon.svg";
import {ReactComponent as EyeIcon} from "../../Assets/icons/eye_icon.svg";

export default function Input({type, placeholder, register, name, options, err}) {
	const [isFocused, setIsfocused] = useState(false);
	const [className, setClassName] = useState("");
	const [internalType, setInternalType] = useState(null);

	useEffect(() => {
		if (err) setClassName("input__input-wrapper input__input-err");
		else if (isFocused) setClassName("input__input-wrapper input__input-focus");
		else setClassName("input__input-wrapper");
	}, [isFocused, err]);

	return (
		<div className={`input ${type === "hidden" && "hidden"}`}>
			<div className={className}>
				<input
					{...register(name, options)}
					type={internalType || type}
					placeholder={placeholder}
					onFocus={() => setIsfocused(true)}
					onBlur={() => setIsfocused(false)}
					autoComplete={type === "password" ? "off" : "on"}
				/>
				{err && (
					<div className="input__icon">
						<ErrIcon />
					</div>
				)}
				{type === "password" && (
					<div
						className="input__icon"
						style={{cursor: "pointer", right: err ? "35px" : "5px"}}
						onClick={() => (!internalType ? setInternalType("text") : setInternalType(null))}
					>
						<EyeIcon />
					</div>
				)}
			</div>
			{err && <span className="input__err-msg">{err}</span>}
		</div>
	);
}
