import {createSlice} from "@reduxjs/toolkit";

const initialState = {
	userDataIsFetching: false,
};

export const flagsSlice = createSlice({
	name: "app",
	initialState,
	reducers: {
		setUserDataIsFetching: (state, action) => {
			state.userDataIsFetching = action.payload;
		},
	},
});

export const {setUserDataIsFetching} = flagsSlice.actions;
export default flagsSlice.reducer;
