import {createApi, fetchBaseQuery} from "@reduxjs/toolkit/query/react";
import {userApi} from "./userApi";
import {setCookie, getCookie} from "../../Util/cookies";

export const authApi = createApi({
	reducerPath: "authApi",
	baseQuery: fetchBaseQuery({baseUrl: "/api/auth/"}),
	endpoints: (builder) => ({
		auth: builder.mutation({
			query: (params) => ({
				url: params.url,
				method: "POST",
				body: params.data,
			}),
			async onQueryStarted({}, {dispatch, queryFulfilled}) {
				const {
					data: {data, success},
				} = await queryFulfilled;
				if (success) {
					setCookie("accessToken", data);
					dispatch(userApi.endpoints.getUser.initiate()).refetch();
				}
			},
		}),
		logout: builder.mutation({
			query: () => ({
				url: "logout",
				method: "POST",
				body: {token: `Barrer ${getCookie("accessToken")}`},
			}),
			async onQueryStarted(undefined, {dispatch, queryFulfilled}) {
				await queryFulfilled;
				dispatch(userApi.util.resetApiState());
				setCookie("accessToken", null);
			},
		}),
	}),
});

export const {useAuthMutation, useLogoutMutation} = authApi;
