import {configureStore} from "@reduxjs/toolkit";
import {setupListeners} from "@reduxjs/toolkit/query";

import styleReducer from "./Reducers/styleSlice";
import flagsSlice from "./Reducers/flagsSlice";

import {productApi} from "./Services/productApi";
import {authApi} from "./Services/authApi";
import {userApi, handleUserRegect} from "./Services/userApi";

export const store = configureStore({
	reducer: {
		style: styleReducer,
		flags: flagsSlice,
		[productApi.reducerPath]: productApi.reducer,
		[authApi.reducerPath]: authApi.reducer,
		[userApi.reducerPath]: userApi.reducer,
	},
	devTools: process.env.NODE_ENV !== "production",
	middleware: (getDefaultMiddleware) =>
		getDefaultMiddleware().concat(productApi.middleware, authApi.middleware, userApi.middleware, handleUserRegect),
});

setupListeners(store.dispatch);
