import {useEffect} from "react";
import {getCookie} from "../Util/cookies";
import {DEFAULT_USER} from "../Util/constants";
import {useLocation, useNavigate} from "react-router-dom";
import {useGetUserQuery} from "../Redux/Services/userApi";

export default function useGetUser() {
	const {data = DEFAULT_USER} = useGetUserQuery(undefined, {skip: getCookie("accessToken") ? false : true});
	const {pathname} = useLocation();
	const navigate = useNavigate();
	useEffect(() => {
		if (!data.fullName && (pathname === "/setting" || pathname === "/orders" || pathname === "/payment")) navigate("/");
	}, [data]);
	return data;
}
