import {useState, useEffect} from "react";
import "./Payment.css";

import {useLocation, useNavigate} from "react-router-dom";
import {AnimatePresence} from "framer-motion";

import {usePayMutation} from "../../Redux/Services/userApi";

import useGetUser from "../../Hooks/useGetUser";

import ProgressBar from "./ProgressBar";
import SubTotal from "./SubTotal";
import CartItem from "../../Components/Cart/CartItem";
import Address from "../../Components/Address/Address";
import CreditCard from "../../Components/CreditCard/CreditCard";
import PaymentStep from "./PaymentStep";
import ErrAlert from "../../Components/ErrAlert";

export default function Payment() {
	const {hash} = useLocation();
	const navigate = useNavigate();
	const [errMssg, setErrMssg] = useState("");
	const [stepCheck, setStepCheck] = useState({});
	const user = useGetUser();
	const [pay] = usePayMutation();

	useEffect(() => {
		if (hash === "#pay" && (!stepCheck.cart || !stepCheck.address)) navigate("#cart");
		if (hash === "#address" && !stepCheck.cart) navigate("#cart");
		if (hash !== "#cart" && hash !== "#address" && hash !== "#pay") navigate("#cart");
	}, [hash]);

	const onClick = () => {
		setErrMssg("");
		switch (hash) {
			case "#cart":
				if (user.cartItems.length > 0) {
					setStepCheck({...stepCheck, cart: true});
					navigate("#address");
				} else setErrMssg("your cart is empty");
				break;
			case "#address":
				if (user.address.some((address) => address.default)) {
					setStepCheck({...stepCheck, address: true});
					navigate("#pay");
				} else setErrMssg("you must to choose address");
				break;
			case "#pay":
				if (user.cards.some((card) => card.default)) handlePay();
				else setErrMssg("you must to choose credit card to pay");
				break;
			default:
				navigate("#cart");
		}
	};

	const handlePay = () => {
		var addressIndex = user.address.findIndex((address) => address.default === true);
		const body = {
			address: user.address[addressIndex],
			items: user.cartItems,
		};
		pay({data: body})
			.unwrap()
			.then((res) => {
				if (res.success) navigate("/orders");
			});
	};

	return (
		<div className="payment page">
			<ProgressBar />
			<AnimatePresence>
				{hash === "#cart" && (
					<PaymentStep
						title="Confirm Items"
						btnTxt="Continue to payment"
						onClick={onClick}
						childrens={
							<>
								<div className="payment__items-container">
									{user.cartItems.length > 0 ? (
										user.cartItems.map((item, i) => <CartItem key={i} item={item} />)
									) : (
										<div className="payment__item-no-items">
											<strong>NO ITEMS IN CART</strong>
										</div>
									)}
								</div>
								<SubTotal />
							</>
						}
					/>
				)}

				{hash === "#address" && (
					<PaymentStep
						title="Select or add a shipping address"
						btnTxt="Continue to payment"
						onClick={onClick}
						childrens={
							<>
								<Address />
								<SubTotal />
							</>
						}
					/>
				)}

				{hash === "#pay" && (
					<PaymentStep
						title="Select your payment method"
						btnTxt="pay"
						onClick={onClick}
						childrens={
							<>
								<CreditCard />
								<SubTotal />
							</>
						}
					/>
				)}
			</AnimatePresence>
			{errMssg !== "" && <ErrAlert text={errMssg} />}
		</div>
	);
}
