import React from "react";
import CurrencyFormat from "react-currency-format";
import useGetUser from "../../Hooks/useGetUser";

const calcTotal = (items) => {
	if (!items || items.length === 0) return 0;
	return parseFloat(items.map((item) => item.productData.price).reduce((item, total) => parseFloat(item) + parseFloat(total)));
};

export default function SubTotal() {
	const user = useGetUser();

	return (
		<div className="payment__subtotal">
			<strong>
				<CurrencyFormat
					renderText={(value) => <>Order Total: {value}</>}
					decimalScale={2}
					value={calcTotal(user.cartItems)}
					displayType={"text"}
					thousandSeparator={true}
					prefix={"$"}
				/>
			</strong>
		</div>
	);
}
