import React from "react";
import "./CartItem.css";

import {Link} from "react-router-dom";

import {useDispatch} from "react-redux";
import {useRemoveUserItemMutation, useUpdateUserItemMutation} from "../../Redux/Services/userApi";
import {changeWishlistDisplay} from "../../Redux/Reducers/styleSlice";
import useGetUser from "../../Hooks/useGetUser";

import {ReactComponent as EditIcon} from "../../Assets/icons/edit_icon.svg";
import {ReactComponent as DeleteIcon} from "../../Assets/icons/delete_icon.svg";

export default function CartItem({item}) {
	const dispatch = useDispatch();
	const [removeFromCart] = useRemoveUserItemMutation();
	const [addToWishlist] = useUpdateUserItemMutation();
	const user = useGetUser();

	const handleSaveForlater = () => {
		const data = {wishlistItems: {...item}};
		delete data["wishlistItems"]["productData"];
		delete data["wishlistItems"]._id;
		const itemExist = user["wishlistItems"].filter(
			(wishlistItem) => wishlistItem.productID === item.productID && wishlistItem.color === item.color,
		)[0];
		if (itemExist) data["wishlistItems"]._id = itemExist._id;
		addToWishlist({type: itemExist ? "edit" : "add", data})
			.unwrap()
			.then((res) => {
				if (res.success) {
					dispatch(changeWishlistDisplay(true));
					removeFromCart({data: {cartItems: {_id: item._id}}});
				}
			});
	};

	return (
		<div className="cart-item">
			<div className="cart-item__line">
				<img src={item.productData.img} />
				<h4>{item.productData.title}</h4>
				<div>
					<Link to={`/item/${item.productData._id}?color=${item.color}&size=${item.size}&qty=${item.qty}`}>
						<EditIcon />
					</Link>
					<button onClick={() => removeFromCart({data: {cartItems: {_id: item._id}}})}>
						<DeleteIcon />
					</button>
				</div>
			</div>
			<div className="cart-item__line">
				<div>
					<span>{item.size}</span>|<span>{item.color}</span>|<span>X{item.qty}</span>
				</div>
				<span>${item.productData.price}</span>
			</div>

			<div className="cart-item__line">
				<button onClick={handleSaveForlater}>Save for later</button>
			</div>
		</div>
	);
}
