import {useState, useRef} from "react";
import "./Address.css";

import {useUpdateUserItemMutation} from "../../Redux/Services/userApi";
import useGetUser from "../../Hooks/useGetUser";

import AddressItem from "./AddressItem";
import Form from "../Form/Form";

export default function Address() {
	const user = useGetUser();
	const [showForm, setshowForm] = useState(false);
	const [updateAddress] = useUpdateUserItemMutation();
	const formRef = useRef();

	const handleEditAddress = (item) => {
		setshowForm(true);
		setTimeout(() => formRef.current.setInputValue({...item}));
	};

	const handleSubmit = (data) => {
		if (!data._id) delete data._id;
		if (!data.default) data.default = false;

		updateAddress({type: data._id ? "edit" : "add", data: {address: data}})
			.unwrap()
			.then((res) => {
				if (res.success) setshowForm(false);
			});
	};

	return (
		<div className="address">
			{user.address.length > 0 ? (
				<div className="address__address-container">
					{user.address.map((item, i) => (
						<AddressItem key={i} item={item} handleEditAddress={handleEditAddress} />
					))}
				</div>
			) : (
				<div className="address__no-address">
					<strong>NO ADDRESS</strong>
				</div>
			)}
			{!showForm && (
				<button className="primary-button" onClick={() => setshowForm(true)}>
					Add
				</button>
			)}
			{showForm && (
				<div className="address__add-address">
					<Form
						ref={formRef}
						inputs={[
							{
								name: "_id",
								type: "hidden",
							},
							{
								name: "default",
								type: "hidden",
							},
							{
								placeholder: "Name",
								name: "addressName",
								required: true,
							},
							{
								placeholder: "City",
								name: "city",
								required: true,
							},
							{
								placeholder: "Street",
								name: "street",
								required: true,
							},
							{
								placeholder: "ZIP Code",
								name: "zipCode",
								required: true,
							},
						]}
						submit={{onSubmit: handleSubmit, submitTxt: "Add"}}
					/>
				</div>
			)}
		</div>
	);
}
